"use client";
import React, { useEffect } from "react";

const page = () => {
  async function getUser() {
    try {
      function fetchFakeUser() {
        return new Promise((resolve) => {
          setTimeout(() => {
            resolve({ name: "Ali" });
          }, 2000); // ⏱️ 10 second delay
        });
      }

      async function getUser() {
        const user = await fetchFakeUser(); // ⏸️ Waits here for 10 seconds

        console.log("User:", user); // ✅ Runs after 10 sec

        function fun1() {
          console.log("this is one function");
        }
        fun1(); // ✅ Also runs after 10 sec
      }
      getUser();
    } catch (err) {
      console.error("Error:", err);
    }
  }


  getUser()
  return <div>hello</div>;
};

export default page;
